/* layout of the todo  */
.todo  {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    width: 375px;
    height: 100vh;
    border-radius: 4px;
    background: #FAFAFA;
    max-height: 667px;
    -webkit-box-shadow: 0 19px 50px rgba(21, 1, 53, 0.5), 
                0 10px 30px rgba(50,50,93,.1), 
                0 5px 15px rgba(0,0,0,.1);
            box-shadow: 0 19px 50px rgba(21, 1, 53, 0.5), 
                0 10px 30px rgba(50,50,93,.1), 
                0 5px 15px rgba(0,0,0,.1);
}
@media screen and (max-width: 425px){
    .todo {
        width: 425px;
        max-height: 100%;
    }
}
.todo nav {
    width: 100%;
    height: 64px;
    background: rgb(136, 85, 221);
    -webkit-box-shadow: 0 2px 5px rgba(21, 1, 53, 0.5);
            box-shadow: 0 2px 5px rgba(21, 1, 53, 0.5);
}
.todo nav ul {
    float: left;
    list-style: none;
    padding-left: 20px;
    margin: 0;
    height: 100%;
}
.todo nav ul li {
    height: 100%;
    line-height: 64px;
}
.todo nav ul li a {
    color: #FAFAFA;
    text-decoration: none;
    font-size: 1.3em;
}


/* create button  */
.create-todo-button-wrapper {
    position: absolute;
    bottom: 30px;
    right: 30px;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background: black;
    display: none;
}
@media screen and (max-width: 425px) {
    .create-todo-button-wrapper {
        display: block;
    }
}



