@import 'https://fonts.googleapis.com/css?family=Yellowtail';
body {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

#root {
    width: 100vw;
    height: 100vh;
    background: -webkit-gradient(linear ,left top, right top ,from(#6885EC), color-stop(#997cca), color-stop(#947a9a), to(#CB8EAB));
    background: -webkit-linear-gradient(left ,#6885EC, #997cca, #947a9a, #CB8EAB);
    background: -o-linear-gradient(left ,#6885EC, #997cca, #947a9a, #CB8EAB);
    background: linear-gradient(left ,#6885EC, #997cca, #947a9a, #CB8EAB);
}




