.nt {
    position: relative;
    margin: 20px auto;
    border-radius: 4px;
    width: 90%;
    height: 40px;
}
.nt .nt-plus-sign {
    position: absolute;
    top: -7px;
    left: 15px;
    font-size: 40px;
    color:  #a9a9a9;
}
.nt input {
    text-indent: 50px;
    font-size: 1.1em;
    width: 100%;
    line-height: 40px;
    border-radius: 4px;
    border: 2px solid #CECED4;
    padding: 0;
    background: #E7DEF0;
}

.nt input:focus {
    outline: none;
    border: 2px solid #776AA8;
}

.nt input:focus + .nt-plus-sign {
    color: #776AA8;
}


